<script>
//单独的组件里面使用
import Vue from "vue";
import infiniteScroll from "vue-infinite-scroll";
new Vue({
  directives: { infiniteScroll }
});
import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState("content", ["readCount", "readList", "loading_text", "busy"]),
    authUser() {
      return this.$store.state.authUser;
    }
  },
  data() {
    return {
      getreadlist: {
        flag: 0,
        pagenum: 1,
        pagesize: 10,
        keywords: "",
        startTime: null,
        endTime: null,
        userid: "0"
      },
      seachbytitle: "",
      dialogVisible: false,
      // 贴子状态筛选
      fliterfeeds: 0,
      // 贴子id和状态（下架， 上墙）
      subjectid: "",
      flag: 0, // 0未读 1 已读
      flags: -1,
      spliceIndex: null, // 删除坐标
      pagenum: 1,
      pagesize: 10,
      checkList: ["垃圾营销"],
      feeds: {
        options: [
          {
            value: 0,
            label: "未读"
          },
          {
            value: 1,
            label: "已读"
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions("content", [
      "getReadList",
      "getReadCount",
      "setUpdateVerify",
      "setUpdateRead",
      "loadMore"
    ]),
    // table操作
    recover() {
      this.$router.push({
        path: "/content/recover"
      });
    },
    read() {
      this.$router.push({
        path: "/content/read"
      });
    },
    // 下架
    async dropoff(id, index, flag) {
      let self = this;
      self.dialogVisible = true;
      self.subjectid = id;
      self.flags = flag;
      self.spliceIndex = index;
    },
    // 更新上墙 下架状态
    async updateVerify() {
      let self = this;
      let res = await self.setUpdateVerify({
        subjectid: self.subjectid,
        flag: self.flags, // -1 下架 1 上墙
        drop_reason: self.checkList // 举报信息
      });
      if (res) {
        self.readList.data.splice(self.spliceIndex, 1);
        self.dialogVisible = false;
        self.seachbytitle = "";
        self.flag = 0;
      }
    },
    // 下拉框
    handleSelect(val) {
      this.flag = val;
      this.getFeedList();
    },
    // 搜索按钮
    bindSearch() {
      this.pagenum = 1;
      this.getFeedList();
    },
    // 获取未读已读列表
    async getFeedList() {
      let self = this;
      self.getreadlist["flag"] = self.flag;
      self.getreadlist["keywords"] = self.seachbytitle;
      self.getreadlist["pagenum"] = self.pagenum;
      self.getreadlist["userid"] = self.authUser.uid;
      await self.getReadList(self.getreadlist);
    },
    // 查看贴子详情
    async toFeeds(id) {
      window.open(`/content/feed/${id}`);
      // this.$router.push({
      //   path: `/content/feed/${id}`
      // });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    // 更新已读
    async updateRead(id) {
      let self = this;
      console.log("id===", id);
      await self.setUpdateRead({
        subjectid: id
      });
    },
    _func() {
      let self = this;
      document.querySelectorAll("#feed>li").forEach(async (x, i) => {
        if (
          Math.abs($("#feed").offset().top) &&
          Math.abs($("#feed").offset().top) + 168 > x.offsetTop + x.offsetHeight
        ) {
          if (
            $("#feed>li")
              .eq(i)
              .attr("status") == "true"
          ) {
            $("#feed>li")
              .eq(i)
              .attr("status", false);
            $("#feed>li .isRead")
              .eq(i)
              .show();
            await self.updateRead(
              $("#feed>li")
                .eq(i)
                .attr("sub")
            );
          }
        }
      });
    },
    _scroll(type) {
      let self = this;
      if (self.authUser.type != 1) {
        document
          .querySelector(".main .page_container .el-scrollbar__wrap")
          .addEventListener("scroll", self._func, false);
      }
    }
  },
  created() {
    let self = this;
    self.getreadlist["userid"] = self.authUser.uid;
    self.getReadList(self.getreadlist);
    self.getReadCount();
  },
  mounted() {
    this._scroll();
  }
};
</script>
